import Vue from 'vue';
import Vuex from 'vuex';
import { saveCookie, getCookie } from '@/utils/cookies';
import { loginUser } from '@/api/auth';

Vue.use(Vuex);

export default new Vuex.Store({
	state: {
		userId: getCookie(process.env.VUE_APP_USER_ID) || '',
		userNm: getCookie(process.env.VUE_APP_USER_NM) || '',
		token: getCookie(process.env.VUE_APP_AUTH_TOKEN) || '',
	},
	getters: {
		isLogin(state) {
			return state.token !== '';
		},
	},
	mutations: {
		setUserNm(state, userNm) {
			state.userNm = userNm;
		},
		setToken(state, token) {
			state.token = token;
		},
		clearUserNm(state) {
			state.userNm = '';
		},
		clearToken(state) {
			state.token = '';
		},
		clearUserId(state) {
			state.userId = '';
		},
		setLoginInfo(state, data) {
			state.userId = data.user.userId;
			state.token = data.authToken;
			state.userNm = data.user.userNm;
		},
		clearLoginInfo(state) {
			state.userId = '';
			state.token = '';
			state.userNm = '';
		},
	},
	actions: {
		async LOGIN({ commit }, params) {
			let { data } = await loginUser(params);

			console.log(data);
			if (data.result == 0) {
				commit('setUserNm', data.data.user.userNm);
				commit('setToken', data.data.authToken);
				saveCookie(process.env.VUE_APP_AUTH_TOKEN, data.data.authToken);
				saveCookie(process.env.VUE_APP_USER_NM, data.data.user.userNm);
				saveCookie(process.env.VUE_APP_USER_ID, data.data.user.userId);
				return data;
			}
		},
	},
});
